.container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
}

.flex_item {
    margin-left: 20px;
    height: 700px;
    width: 230px;
}

.movie_summary {
    display: -webkit-box;
    word-wrap: break-word;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
}